<template>
  <div class="wyl_big">
    <!-- header  面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- header end -->

    <!-- main  卡牌试图区域   start   -->
    <el-card>
      <!-- 添加角色 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <tree-table
        class="tree-table"
        :data="CategoriesList"
        :show-row-hover="false"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
      >
        <!-- 是否有效  icon -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-error" style="color:red" v-if="scope.row.cat_deleted==true"></i>
          <i class="el-icon-success" style="color:green" v-if="scope.row.cat_deleted==false"></i>
        </template>

        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level==0">一级</el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level==1">二级</el-tag>
          <el-tag type="warning" size="mini" v-else-if="scope.row.cat_level==2">三级</el-tag>
        </template>

        <!-- 操作 -->
        <template slot="opt" slot-scope="scope">
          <el-button
            @click="showEditDialog(scope.row)"
            size="mini"
            type="primary"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            @click="showEditDialog(scope.row)"
            size="mini"
            type="danger"
            icon="el-icon-delete"
          ></el-button>
        </template>
      </tree-table>

      <!-- 分页器 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="Total"
      ></el-pagination>
    </el-card>
    <!-- main  卡牌试图区域   end   -->

    <!--    模态框 添加分类的  start -->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      @close="addCateDialogClosed"
      width="50%"
    >
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="70px"
      >
        <el-form-item label="分类名称:" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类:">
          <!-- option用来制定数据源  props 用来制定配置对象 -->
          <el-cascader


          
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="ParentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
    <!--    模态框 添加分类的  end -->
  </div>
</template>

      

  

<script>
export default {
  name: "",
  data() {
    return {
      // 页面上的货物种类
      CategoriesList: [],
      //  页面上的货物种类的需要查询的参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 2
      },
      // 分页器  总数据条数
      Total: 0,
      //   tree-table  为table指定列的定义
      columns: [
        {
          label: "分类名称",
          prop: "cat_name"
        },
        {
          label: "是否有效",
          //  定义为模板列
          type: "template",
          //   模板咧使用的模板名称
          template: "isok"
        },
        {
          label: "排序",
          //  定义为模板列
          type: "template",
          //   模板咧使用的模板名称
          template: "order"
        },
        {
          label: "操作",
          //  定义为模板列
          type: "template",
          //   模板咧使用的模板名称
          template: "opt"
        }
      ],
      //   模态框 添加分类的  显示隐藏
      addCateDialogVisible: false,
      //   模态框 添加分类的  表单数据对象
      addCateForm: {
        // 分类名称
        cat_name: "",
        // 父级分类的 id
        cat_pid: 0,
        // 等级分类
        cat_level: 0
      },
      //   模态框 添加分类的 验证规则
      addCateFormRules: {
        // 分类名称
        cat_name: [
          { required: true, message: "请输入分类名称", trigger: "blur" }
        ]
      },
      //   模态框 添加分类的 父级分类的列表 数据
      ParentCateList: [],
      //   模态框 添加分类的 父级分类的列表 指定级联选择器的配置对象
      cascaderProps: {
        value: "cat_id", //指向选中的值
        label: "cat_name", //指向看到的值
        children: "children" //父子嵌套的属性
      },
      //模态框 添加分类的 父级分类的列表  选中的父级分类的id数组
      selectedKeys: []
    };
  },
  props: {},
  components: {},
  mounted() {
    this.getCategoriesList();
  },
  methods: {
    //   页面上的货物种类
    async getCategoriesList() {
      let { data: res } = await this.$http.get(`categories`, {
        params: this.queryInfo
      });
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("商品分类接口找不到哦");
      }
      this.CategoriesList = res.data.result;
      console.log(this.CategoriesList);
      this.Total = res.data.total;
    },
    // 分页器 监听pageSize 改变
    handleSizeChange(a) {
    //   console.log(a);
      this.queryInfo.pagesize = a;
      //   页面数据接口重新渲染
      this.getCategoriesList();
    },
    // 分页器 监听页码值 改变
    handleCurrentChange(a) {
    //   console.log(a);
      this.queryInfo.pagenum = a;
      //   页面数据接口重新渲染
      this.getCategoriesList();
    },
    //   模态框 添加分类的  点击事件
    async showAddCateDialog() {
      //获取 模态框 添加分类的   父级分类的数据列表
      this.getParentCateList();
      //   模态框展示
      this.addCateDialogVisible = true;
    },
    //获取 模态框 添加分类的   父级分类的数据列表
    async getParentCateList() {
      let { data: res } = await this.$http.get("categories", {
        params: {
          type: 2
        }
      });
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success(res.meta.msg);
      //   模态框 添加分类的 父级分类的列表 数据
      this.ParentCateList = res.data;
      //   console.log(res.data);
    },
    // 模态框 添加分类的 父级分类的列表中数据发生变化的时候 触发的函数
    parentCateChanged() {
      console.log(this.selectedKeys);
      let a = this.selectedKeys.length;
      console.log(a);
      if (a > 0) {
        //   父级分类的id
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ];
        // 当前分类的等级赋值
        this.addCateForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        //   父级分类的id
        this.addCateForm.cat_pid = 0;
        // 当前分类的等级赋值
        this.addCateForm.cat_level = 0;
      }
    },
    // 点击按钮，添加新的分类
    addCate() {
      this.$refs.addCateFormRef.validate(async vaild => {
        if (!vaild) return;

        let { data: res } = await this.$http.post(
          `categories`,
          this.addCateForm
        );
        console.log(res);
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg);
        }
        this.$message.success(res.meta.msg);
        this.getCategoriesList();
        this.addCateDialogVisible = false;
      });
    },
    // 监听对话框的关闭事件，重置表单数据
    addCateDialogClosed() {
      // 清空表单内容
      this.$refs.addCateFormRef.resetFields();
      // 选中的父级分类的id数组 清空
      (this.selectedKeys = []),
        // 表单数据对象 里面的样式清空
        (this.addCateForm.cat_level = 0);
      this.addCateForm.cat_pid = 0;
    }
  }
};
</script>

<style scoped lang='scss'>
.tree-table {
  margin-top: 0.15rem;
}
.el-cascader {
  width: 100%;
  height: 2rem;
}
</style>